<template>
    <div class='Pic'>
        <div class="header">
            <span>颜色</span>
            <span>车款</span>
        </div>
        <div class="body">
            <ul>
                <li v-for="(it,ia) in pic_list" :key='ia'>
                    <img v-for="(it,i) in it.List" :key='i' v-lazy="pic_list_url[ia][i]">
                    <div @click='show_pic(it)'>
                        <p>{{it.Name}}</p>
                        <p>{{it.List.length}}张</p>
                    </div>
                </li>
            </ul>
        </div>
        <router-view />
    </div>
</template>
<script>
export default {
    name:'Pic',
    data(){
        return {
            pic_list_url:new Array(),
            pic_list:'',
            SerialID:this.$route.params.SerialID
        };
    },
    created(){
        if(!this.$route.params.pic_list){
            this.$router.back();
        }else{
            this.pic_list = this.$route.params.pic_list;
            console.log(this.pic_list);
            this.filterUrl(this.$route.params.pic_list);
        }
    },
    methods:{
        show_pic(it){
            this.$router.push({
                name:'imgList',
                query:{
                    SerialID:this.SerialID,
                    ImageID:it.Id
                }
            })
        },
        filterUrl(list){
            for (let i = 0; i < list.length; i++) {
                this.pic_list_url.push(new Array());
                list[i].List.forEach(e => {
                    let arr = e.Url.split('{0}');
                    this.pic_list_url[i].push(arr[0] + 1 + arr[1]);
                })   
            }
        }
    },
    beforeRouteEnter(to,from,next){
        next(vm => {
            if(!vm.$route.params.pic_list){
                vm.$router.back();
            }
        })
    }
}
</script>
<style lang='stylus' scoped>
    .Pic
        position absolute
        top 0
        left 0
        width 100%
        height 100%
        background-color #f4f4f4
        .header
            width 100%
            height 50px
            span 
                border 1px solid #f4f4f4
                display inline-block
                width 50%
                height 100%
                line-height 50px
                text-align center
                background-color white
        .body
            overflow auto
            height 100%
            ul
                height 106%
                position relative
                li
                    position relative
                    img 
                        width 32%
                        margin 2px
                        height 76px
                    div
                        height 76px
                        position absolute
                        top 2px
                        left 2px
                        width 32%
                        z-index 20
                        background-color rgba(0,0,0,.5)
                        color white
                        display flex
                        justify-content center
                        align-items center
</style>